html {
  box-sizing: border-box;
}

body {
  background-color: #222;
  color: #fff;
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  outline: 0;
}

.container {
  max-width: 375px;
  min-height: 100vh;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}
