.wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-right: 16px;
  padding-left: 16px;
  margin-top: 16px;
  margin-bottom: 16px;
}

.wrapper > * {
  flex: 1;
}

.inputWrapper {
  border: 5px solid #fff;
  position: relative;
  margin-top: 24px;
  margin-bottom: 24px;
}

.inputWrapper:before,
.inputWrapper:after {
  content: ' ';
  position: absolute;
  left: 24px;
  right: 24px;
  background-color: #222;
  height: 7px;
}

.inputWrapper:before {
  top: -6px;
}

.inputWrapper:after {
  bottom: -6px;
}

.input {
  background-color: transparent;
  color: #fff;
  width: 100%;
  font-size: 36px;
  border: none;
  padding: 24px;
  text-align: center;
  border-radius: 4px;
}
